body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.BusSchedule {
	padding: 16px;
	min-height: 500px;
	height: calc(100vh - 85px);
	overflow: hidden;
	background-color: rgb(24, 24, 24);
}
.BusSchedule__inner {
	overflow: scroll;
}
.BusRow {
	display: -webkit-flex;
	display: flex;
	position: relative;
	background-color: rgb(40, 40, 40);
	padding: 10px;
	height: 30px;
	width: calc(100% + 250px);
	min-width: 1000px;
	border-bottom: 1px solid #272727;
	-webkit-transition: 0.3s ease;
	transition: 0.3s ease;
}
.BusRow.is-hoverable:hover {
	background-color: rgb(32, 32, 32);
	cursor: pointer;
	border-bottom: 1px solid #272727;
}
.BusRow:nth-child(even) {
	background-color: rgb(52, 52, 52);
}
.BusRow:last-child {
	border-bottom: 1px solid transparent;
}
.BusRow__left {
	color: #999;
	padding: 0;
	min-width: 160px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	border-right: 1px solid #444;
}
.BusRow__left,
.BusRow__right {
	position: relative;
	display: -webkit-flex;
	display: flex;
	height: 100%;
	-webkit-align-items: center;
	        align-items: center;
}
.BusRow__time-window {
	text-align: right;
	font-size: 12px;
	margin-right: 12px;
}
.BusRow__trip {
	position: absolute;
	padding: 5px 0;
	font-size: 12px;
	color: white;
	cursor: pointer;
	text-align: center;
	display: inline-block;
	background-color: #696969;
	box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
}
.BusRow__trip:hover,
.BusRow__trip:active,
.BusRow__trip:focus {
	background-color: #818181;
	box-shadow: 0 0 12px rgba(0, 0, 0, 0.3);
}
.BusRow__trip.is-selected {
	background-color: #e3c328;
	color: #000;
	font-weight: 600;
}
.BusSchedule__timeline {
	position: relative;
	bottom: 10px;
	display: block;
	height: 30px;
	width: calc(100% - 171px);
	left: 171px;
}
.ScheduleTimeline__hour {
	position: absolute;
	display: inline-block;
	color: #999;
	bottom: 0;
	font-size: 12px;
}
.BusSchedule__error,
.BusSchedule__message {
	color: rgb(226, 226, 226);
	text-align: center;
}
@media screen and (min-width: 992px) {
	.BusRow {
		width: 100%;
		min-width: unset;
	}
	.BusSchedule__inner {
		overflow: hidden;
	}
}

.AppBody__header {
	height: 60px;
	background-color: rgb(18, 18, 18);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	/* font-size: calc(10px + 2vmin); */
	font-size: 20px;
	color: white;
}
.AppBody__header h4 {
	margin: 0;
}

